import $ from 'jquery';
import { roomItem } from './room-item';

export const roomsList = (availableRooms) => {
    const fragment = $(new DocumentFragment());
    const roomsListWrap = $('<section id="roomsList"></section>');
    const roomsListIntro = $(`<div class="roomsListIntro">
                                <h1>Pokoje</h1>
                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
                            </div>`);
    const roomsListCont =  $('<div id="roomsListCont"></div>');

    const availableRoomsToDisplay = availableRooms.map(roomData => {
        return roomItem(roomData);
    });
    roomsListCont.prepend(availableRoomsToDisplay);
    roomsListWrap.prepend(roomsListCont).prepend(roomsListIntro);
    fragment.append(roomsListWrap);

    return fragment;
};